const WorkEls = Array.from(document.getElementsByClassName("Work"))
console.log(document.getElementsByClassName("Work"))
console.log(WorkEls)
function HandleHover(Element){
  Element.target.querySelector(".Header").style["color"] = "rgba(255,255,255,1)"
  Element.target.querySelector(".Desc").style["color"] = "rgba(255,255,255,1)"
  Element.target.querySelector(".Darken").style["background-color"] = "rgba(0,0,0,0.5)"
}
function HoverEnd(Element){
  Element.target.querySelector(".Header").style["color"] = "rgba(255,255,255,0)"
  Element.target.querySelector(".Desc").style["color"] = "rgba(255,255,255,0)"

  Element.target.querySelector(".Darken").style["background-color"] = "rgba(0,0,0,0)"
}
for (let i of WorkEls) {
  i.querySelector(".Header").style["color"] = "rgba(255,255,255,0)"
  i.querySelector(".Desc").style["color"] = "rgba(255,255,255,0)"
  i.querySelector(".Darken").style["background-color"] = "rgba(0,0,0,0)"
  i.addEventListener("mouseenter",HandleHover)
  i.addEventListener("mouseleave",HoverEnd)
}
